@import url(https://fonts.googleapis.com/css?family=Raleway:400,500,600|Roboto:300&display=swap&subset=cyrillic);
*,*:before,*:after{padding:0;margin:0;border:0;box-sizing:border-box}html,body{height:100%;font-family:'Raleway', sans-serif}.container{max-width:1200px;padding:0 10px;margin:0 auto;position:relative}

.App{color:red}

.header-wrapper{display:flex;justify-content:space-between;height:106px}.header__icons{display:flex;justify-content:space-between;margin-top:65px;width:90px}.header__logo{margin-top:5px}.menu__list{display:flex;justify-content:space-around;margin:0;text-align:center}.menu__item{list-style:none;padding:0 4%;height:95px;border-left:1px solid rgba(62,62,62,0.22)}.menu__item:first-child{border:none}.menu__link{display:block;margin-top:65px;text-decoration:none;color:#3E3E3E;font-weight:500;font-size:18px;line-height:21px}.menu__link_with-arrow::after{content:'';display:inline-block;position:relative;margin-left:10px;width:14px;height:8px;background:url("/img/icons/arrow-down.png") no-repeat center center}

.search-bar{position:absolute}.search-bar__input{background-color:#F1F1F1;height:40px;width:300px;position:relative;top:19px;right:250px;-webkit-animation:inp 0.5s linear;animation:inp 0.5s linear;z-index:1}.search-bar__image{position:relative;z-index:2}.search-bar__image:hover{cursor:pointer}.anim{-webkit-animation:inp2 1s ease;animation:inp2 1s ease;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}@-webkit-keyframes inp{0%{top:-20px;opacity:0}100%{top:19px;opacity:1}}@keyframes inp{0%{top:-20px;opacity:0}100%{top:19px;opacity:1}}@-webkit-keyframes inp2{0%{top:0}100%{top:50px}}@keyframes inp2{0%{top:0}100%{top:50px}}

.langs{position:relative;margin-left:50px}.langs__flag{margin:10px 0}.langs__flag:hover{cursor:pointer}.langs__flag_main{margin-left:10px;margin-bottom:20px;margin-top:0}.langs__list{background:rgba(62,62,62,0.5);padding:0 10px;list-style:none;display:none}.langs__list_open{display:block}

.hero{height:448px;background-color:green;display:flex;align-content:center;flex-flow:row wrap;background-size:cover}.hero__description{background-color:rgba(62,62,62,0.9);width:751px;height:260px;padding:65px 0px 65px 90px}.hero__description::before{content:'';display:block;height:2px;width:636;position:relative;top:70px;right:90px;background-color:rgba(228,181,107,0.8)}.hero__text{font-weight:500;font-size:40px;line-height:65px;color:#fff}.buttons-section{display:flex;justify-content:center;width:100%;position:relative;top:70px}.buttons-section__button{height:14px;width:14px;background-color:#ffffff;margin:0px 15px;border-radius:50%}

.services{overflow:hidden}.services__gear{display:block;position:absolute;z-index:-1;transform:rotate(var(--deg))}.services__gear_small-1{background:url("/img/pics/gear_small.png") center center no-repeat;height:180px;top:460px;left:-75px;width:180px}.services__gear_small-2{background:url("/img/pics/gear_small.png") center center no-repeat;height:180px;top:230px;right:-80px;width:180px}.services__gear_medium-1{background:url("/img/pics/gear_medium.png") center center no-repeat;height:210px;top:475px;right:-50px;width:210px}.services__gear_medium-2{background:url("/img/pics/gear_medium.png") center center no-repeat;height:210px;top:250px;left:-75px;width:210px}.services__gear_big-1{background:url("/img/pics/gear_big.png") center center no-repeat;height:370px;top:230px;right:50px;width:370px}.services__gear_big-2{background:url("/img/pics/gear_big.png") center center no-repeat;height:370px;top:150px;left:50px;width:370px}.services__tittle{font-weight:600;font-size:40px;line-height:60px;text-align:center;color:#3E3E3E;margin:30px 0 30px}.services__tittle::after{height:2px;width:82px;content:'';display:block;position:relative;background:#E4B56B;margin:5px auto 0}.services__text{color:#ffffff;font-weight:300;font-size:20px;line-height:60px;padding:0 35px 0 15px;background-color:rgba(62,62,62,0.9);max-width:333px;height:56px;letter-spacing:0.6px;word-spacing:1px}.services__text::after{content:'';display:block;background-color:#E4B56B;left:280px;bottom:38px;height:2px;width:46px;position:relative;transform:rotate(90deg)}.services__row{display:flex;justify-content:center;flex-flow:row wrap;width:100%;justify-content:space-between}.services__card{background-size:cover;margin:5px 0;padding-top:33px;flex:0 0 33%;height:100%;height:202px}.services__card_big{flex:0 0 49.8%}.services__button{display:block;margin:15px auto;padding:10px 0px;font-weight:300;font-size:20px;line-height:23px;text-align:center;color:rgba(255,255,255,0.8);letter-spacing:0.8px;background-color:#454647;width:380px}

.our-company{color:#ffffff}.our-company__tittle{font-weight:600;font-size:40px;line-height:60px;text-align:center;color:#3E3E3E;margin:40px 0 25px}.our-company__tittle::after{height:2px;width:82px;content:'';display:block;position:relative;background:#E4B56B;margin:5px auto 0}.our-company__why{height:380px;width:100%;background:url("/img/pics/our_company-1.png") center center no-repeat;background-size:cover;margin-bottom:30px}.our-company__advent{height:380px;width:100%;background:url("/img/pics/our_company-2.png") center center no-repeat;background-size:cover}.our-company__card{max-width:750px;width:90%;background-color:rgba(62,62,62,0.9);min-height:260px;padding-top:20px}.our-company__card_left{padding:0 8% 0 7%}.our-company__card_left::before{height:2px;width:212px;content:'';display:block;position:relative;background:#E4B56B;transform:rotate(90deg);left:90%;top:106px}.our-company__card_right{padding:0 7% 0 9%;margin-left:auto}.our-company__card_right::before{height:2px;width:212px;content:'';display:block;position:relative;background:#E4B56B;transform:rotate(90deg);right:30%;top:106px}.our-company__card-wrapper{display:flex;align-items:center;width:100%;height:100%}.our-company__tittle-card{margin-top:15px;font-weight:500;font-size:40px;line-height:60px}.our-company__text{margin-top:25px;font-weight:500;font-size:18px;line-height:23px}

.footer{background-color:#F8F8F8;padding-top:30px}.footer__row{display:flex}.footer__block{flex:0 0 30%}.footer__block_big{flex:0 0 40%}.footer__list{display:flex;flex-direction:column;flex-wrap:wrap;align-content:flex-start;align-content:space-between;height:170px}.footer__item{list-style:none;margin:10px 0}.footer__tittle{color:#3E3E3E;font-weight:500;font-size:18px;line-height:21px}.footer__link{color:#aeaeae;font-weight:500;font-size:16px;line-height:21px;text-decoration:none}.footer__social{margin-top:15px;width:150px;display:flex;justify-content:space-around}.footer__item-social{list-style:none}.footer__facebook{display:block;width:21px;height:21px;background:url("/img/icons/face.png") center center no-repeat}.footer__tube{display:block;width:21px;height:21px;background:url("/img/icons/youtube.png") center center no-repeat}.footer__gplus{display:block;width:21px;height:21px;background:url("/img/icons/gplus.png") center center no-repeat}.footer__insta{display:block;width:21px;height:21px;background:url("/img/icons/insta.png") center center no-repeat}.contacts{padding-left:5%;font-weight:normal;font-size:16px;line-height:21px;color:#3E3E3E}.contacts__num{font-family:Roboto;font-style:normal;font-weight:300;font-size:16px;line-height:21px;color:#3E3E3E}.contacts__phone{margin-top:15px}.contacts__worktime{margin-top:15px}.contacts__adress{margin-top:15px}

